body{
    background-color: #b8bddd;
}
.main-card--container{
 
}
.card{
    background-color: #aaa;
    width: 28%;
    height: 90vh;
    margin: 25px;
    float: left;
}
.cord-circel{
    margin: 20px;
    border: 1px solid #fff;
    border-radius: 50%;
    padding: 10px;

}
.card-author{
    display: block;
    font-size: 12px;
    letter-spacing: 0.5px;
    margin: 15px 0 0;
    text-transform: uppercase;
}
.card-title{
    font-family: "Cormorant Garamond", serif;
    font-size: 60px;
    font-weight: 300;
    line-height: 60px;
    margin: 10px 0;
    text-transform: capitalize;
}
.card-description{
    font-weight: 300;
    line-height: 22px;
    margin: 10px 0;
}
.card-read{
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 6px;
    margin: 5px 0 20px;
    position: relative;
    text-align: right;
    text-transform: uppercase;
}
.card-read:after{
    background-color: #b8bddd;
    content: "";
    display: block;
    height: 1px;
    position: absolute;
    top: 9px;
    width: 75px;
}
.card-tag{
    float: right;
    margin: 5px 0 0 ;
    border: 1px solid #aaa;
    padding: 5px 10px;
    cursor: pointer;
}
.card-tag:hover{
    background: #aaa;
    color: black;
}
.card-media{
    max-width: 100%;
    height: auto;
}
.card-shadow{
    background-color: #fff;
    box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 1), 0 2px 50px 2px rgba(0, 0, 0, 1),0 0 100px 3px rgba(0, 0, 0, 25);
    height: 1px;
    margin: -1px auto 0;
    width: 80%;
    z-index: -1;
}
@media (max-width:998px){
    .mnin-card--cointainer{
        display: grid;
        grid-template-columns: repeat(1,minmax(200px, 500px));
        grid-gap: 50px;
        justify-content: space-around;
    }
}
/* button style */
.navbar{
    padding: 50px;
    text-align: center;
}
.navbar h1{
    padding-bottom: 20px;
    text-transform: capitalize;
}
/* button group style */
.btn-group{
    border-radius: 1rem;
    text-transform: capitalize;

}
.btn-group__item {
    border: none;
    background-color: rgb(255, 255, 255);
    cursor: pointer;
    margin: 0;
    font-size: 17px;
    box-shadow: inset 0px 0px 0px -15px rebeccapurple;
    transition: all 300ms ease-out;
    text-transform: capitalize; 
    padding: 20px;
}
.btn-group__item:last-child{
    border-top-right-radius: 1rem;
    
}
.btn-group__item:first-child{
    border-bottom-left-radius: 1rem;
    
}
.btn-group__item:hover,
j.btn-group__item:focus{
    color:rebeccapurple;
    box-shadow: inset 0px -20px 0px -15px rebeccapurple;

}
.btn-group__item:focus{
    outline: none;
}
.btn-group__item:after{
    content: "ok";
    margin-left: 0.5rem;
    display: inline-block;
    color: rebeccapurple;
    position: absolute;
    transform: translateY(10px);
    opacity: 0;
    transition: all 200ms ease-out;
}
.btn-group__item--active:after{
    opacity: 1;
    transform: translateY(-2px);
}